@charset "UTF-8";
@font-face {
  font-family: 'Ubuntu';
  font-style: normal;
  font-weight: 400;
  src: local("Ubuntu"), url(../../fonts/Ubuntu-Medium.ttf); }

@font-face {
  font-family: 'Ubuntu Condensed';
  font-style: normal;
  font-weight: 400;
  src: local("Ubuntu Condensed"), url(../../fonts/UbuntuCondensed-Regular.ttf); }

body {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
  line-height: 18px; }

.page-header {
  padding-top: 30px;
  margin-bottom: 40px; }
  .page-header__inner {
    padding-left: 20px;
    padding-right: 20px; }
  .page-header__logo {
    width: 200px;
    height: 50px;
    margin-bottom: 30px;
    background-color: #cccccc; }
  .page-header__nav-wrapper {
    width: 100%;
    min-height: 58px;
    background-color: #35383c;
    position: relative; }
  .page-header__nav-toggler-wrapper {
    float: right;
    width: 60px;
    min-height: 58px;
    position: relative;
    cursor: pointer;
    background-color: #248cec;
    z-index: 5; }
    .page-header__nav-toggler-wrapper:hover {
      background-color: #1272cb; }
    .page-header__nav-toggler-wrapper:after {
      display: block;
      content: '';
      clear: both; }

@media (min-width: 768px) {
  .page-header__nav-toggler-wrapper {
    display: none; } }

.nav-toggler {
  display: block;
  position: absolute;
  width: 60%;
  height: 5px;
  top: 30%;
  right: 50%;
  margin-right: -30%;
  background-color: white;
  outline: none;
  font-size: 0px;
  border: none;
  cursor: pointer; }
  .nav-toggler:before {
    content: "";
    display: block;
    position: absolute;
    top: 10px;
    width: 100%;
    left: 0;
    height: 5px;
    background-color: white; }
  .nav-toggler:after {
    content: "";
    display: block;
    position: absolute;
    top: 20px;
    width: 100%;
    left: 0;
    height: 5px;
    background-color: white; }
  .nav-toggler:after, .nav-toggler:before, .nav-toggler {
    transition: all .3s ease-in-out; }
  .nav-toggler_nav-opened {
    background-color: white; }
    .nav-toggler_nav-opened:after {
      transform: rotate(-45deg);
      top: 10px; }
    .nav-toggler_nav-opened:before {
      transform: rotate(45deg); }

.main-nav {
  display: none;
  clear: both;
  position: absolute;
  width: 100%;
  background-color: #35383c; }
  .main-nav__list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-direction: column; }
    .main-nav__list li:last-of-type .main-nav__list-item {
      border-bottom: none; }
  .main-nav__list-item {
    display: block;
    padding: 20px 40px;
    color: white;
    font-family: "Ubuntu Condensed", sans-serif;
    text-transform: uppercase;
    text-decoration: none;
    border-bottom: 1px solid white; }
    .main-nav__list-item:hover {
      color: #f4b60d; }

@media (min-width: 768px) {
  .main-nav {
    display: block; }
    .main-nav__list {
      flex-direction: row; }
      .main-nav__list li:first-child {
        background-color: #248cec; }
    .main-nav__list-item {
      border-bottom: none; } }

main {
